<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>
    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId"
        :colorId.sync="filterColorId"
      />
      <section class="catalog">
        <div v-if="productsLoading">
          <div class="spiner">
            <svg xmlns:svg="http://www.w3.org/2000/svg" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.0" width="40px" height="40px" viewBox="0 0 128 128" xml:space="preserve"><g><circle cx="16" cy="64" r="16" fill="#9EFF32"/><circle cx="16" cy="64" r="14.344" fill="#9EFF32" transform="rotate(45 64 64)"/><circle cx="16" cy="64" r="12.531" fill="#9EFF32" transform="rotate(90 64 64)"/><circle cx="16" cy="64" r="10.75" fill="#9EFF32" transform="rotate(135 64 64)"/><circle cx="16" cy="64" r="10.063" fill="#9EFF32" transform="rotate(180 64 64)"/><circle cx="16" cy="64" r="8.063" fill="#9EFF32" transform="rotate(225 64 64)"/><circle cx="16" cy="64" r="6.438" fill="#9EFF32" transform="rotate(270 64 64)"/><circle cx="16" cy="64" r="5.375" fill="#9EFF32" transform="rotate(315 64 64)"/><animateTransform attributeName="transform" type="rotate" values="0 64 64;315 64 64;270 64 64;225 64 64;180 64 64;135 64 64;90 64 64;45 64 64" calcMode="discrete" dur="880ms" repeatCount="indefinite"></animateTransform></g></svg>  <svg width="30" height="21" fill="currentColor"></svg>
          </div>
        </div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров...
        <button v-on:click.prevent="loadProducts">Попробовать еще раз</button>
        </div>
        <!-- передача по цепочке событий     <ProductList :products="products" @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)"/> -->
        <ProductList :products="products" />
        <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
// import products from '@/data/products';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '../config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,
      page: 1,
      productsPerPage: 6,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    // filteredProducts() {
    //   let filteredProducts = products;
    //   if (this.filterPriceFrom > 0) {
    //     // eslint-disable-next-line max-len
    //     filteredProducts = filteredProducts.filter(
    //       (product) => product.price > this.filterPriceFrom,
    //     );
    //   }
    //   if (this.filterPriceTo > 0) {
    //     filteredProducts = filteredProducts.filter(
    //       (product) => product.price < this.filterPriceTo,
    //     );
    //   }
    //   if (this.filterCategoryId) {
    //     // eslint-disable-next-line max-len
    //     filteredProducts = filteredProducts.filter(
    //       (product) => product.сategoryId === this.filterCategoryId,
    //     );
    //   }
    //   if (this.filterColorId) {
    //     // eslint-disable-next-line max-len
    //     filteredProducts = filteredProducts.filter(
    //       (product) => product.colorId
    //         && product.colorId.indexOf(this.filterColorId) !== -1,
    //     );
    //   }
    //   return filteredProducts;
    // },
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        }))
        : [];
      // запрос на локальный список
      // const offset = (this.page - 1) * this.productsPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productsPerPage);
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          // eslint-disable-next-line no-template-curly-in-string
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId,
            },
          })
          // eslint-disable-next-line no-return-assign
          .then((response) => {
            console.log(response);
            this.productsData = response.data;
          })
          // eslint-disable-next-line no-return-assign
          .catch(() => (this.productsLoadingFailed = true))
          // eslint-disable-next-line no-return-assign
          .then(() => (this.productsLoading = false));
      }, 0);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>

<style scoped>
.spiner {
  text-align: center;
}
</style>
