/* eslint-disable consistent-return */
/* eslint-disable max-len */
/* eslint-disable arrow-body-style */
/* eslint-disable no-shadow */
import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '../config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartProductsLoad: false,
    cartProductsLoadFailed: false,
    orderInfo: null,
  },
  mutations: {
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((item) => item.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((items) => items.productId !== productId);
    },
    updateUserAccesKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        };
      });
    },
  },
  getters: {
    cartDetailsProducts(state) {
      // eslint-disable-next-line arrow-parens
      return state.cartProducts.map(item => {
        const { product } = state.cartProductsData.find((p) => p.product.id === item.productId);

        return {
          ...item,
          // eslint-disable-next-line arrow-parens
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailsProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
    amountProduct(state, getters) {
      return getters.cartDetailsProducts.reduce((acc, item) => item.amount + acc, 0);
    },
    resOrderInfo(state) {
      const amountAll = state.orderInfo?.basket.items.reduce((acc, item) => acc + item.quantity, 0);
      // eslint-disable-next-line prefer-object-spread
      return Object.assign({}, state.orderInfo, { amount: amountAll });
    },
  },
  actions: {
    loadOrderInfo({ commit, state }, orderId) {
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: state.userAccessKey,
          },
        })
        .then((response) => commit('updateOrderInfo', response.data))
        .catch((err) => console.log('loadOrderInfo => ', err));
      // .then((response) => {
      //   context.commit('updateOrderInfo', response.data);
      // });
    },
    loadCart({ commit, state }) {
      state.cartProductsLoad = true;
      state.cartProductsLoadFailed = false;
      clearTimeout(this.cartProductsTimer);
      this.cartProductsTimer = setTimeout(() => {
        return axios
          .get(`${API_BASE_URL}/api/baskets`, {
            params: {
              userAccessKey: state.userAccessKey,
            },
          })
          .then((response) => {
            if (!state.userAccessKey) {
              localStorage.setItem('userAccessKey', response.data.user.accessKey);
              commit('updateUserAccesKey', response.data.user.accessKey);
            }
            commit('updateCartProductsData', response.data.items);
            commit('syncCartProducts');
          })
          // eslint-disable-next-line no-return-assign
          .catch(() => {
            state.cartProductsLoadFailed = true;
          })
          // eslint-disable-next-line no-return-assign
          .then(() => state.cartProductsLoad = false);
      }, 1000);
    },
    addProductToCart(context, { productId, amount }) {
      // eslint-disable-next-line no-promise-executor-return
      return (new Promise((resolve) => setTimeout(resolve, 1000)))
        .then(() => {
          return axios
            .post(`${API_BASE_URL}/api/baskets/products`, {
              productId,
              quantity: amount,
            }, {
              params: { userAccessKey: context.state.userAccessKey },
            })
            .then((response) => {
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts');
            });
        });
    },

    deleteCartToProduct(context, productId) {
      context.commit('deleteCartProduct', productId);
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          data: { productId },
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => context.commit('updateCartProductsData', response.data.items))
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },

    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });
      if (amount < 1) {
        return;
      }

      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: { userAccessKey: context.state.userAccessKey },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
  },
});
