<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div class="form__counter">
    <button
      type="button"
      v-on:click.prevent="productReduce()"
      aria-label="Убрать один товар"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>
    <input readonly type="text" v-model.number="productAmount" />
    <button
      type="button"
      v-on:click.prevent="productAdd()"
      aria-label="Добавить один товар"
    >
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>
<script>
export default {
  props: ['amount'],
  data() {
    return {
      productAmount: 0,
    };
  },
  model: {
    prop: 'productAmount',
    event: 'changeAmount',
  },
  methods: {
    productAdd() {
      if (this.productAmount >= 0) {
        this.productAmount += 1;
        this.$emit('changeAmount', this.productAmount);
      }
    },
    productReduce() {
      if (this.productAmount > 0) {
        this.productAmount -= 1;
        this.$emit('changeAmount', this.productAmount);
      }
    },
  },
};
</script>
