<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }} {{ correctNameProdLabel() }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <div v-if="cartProductsLoad">
              <div class="spiner">
            <svg xmlns:svg="http://www.w3.org/2000/svg" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.0" width="40px" height="40px" viewBox="0 0 128 128" xml:space="preserve"><g><circle cx="16" cy="64" r="16" fill="#9EFF32"/><circle cx="16" cy="64" r="14.344" fill="#9EFF32" transform="rotate(45 64 64)"/><circle cx="16" cy="64" r="12.531" fill="#9EFF32" transform="rotate(90 64 64)"/><circle cx="16" cy="64" r="10.75" fill="#9EFF32" transform="rotate(135 64 64)"/><circle cx="16" cy="64" r="10.063" fill="#9EFF32" transform="rotate(180 64 64)"/><circle cx="16" cy="64" r="8.063" fill="#9EFF32" transform="rotate(225 64 64)"/><circle cx="16" cy="64" r="6.438" fill="#9EFF32" transform="rotate(270 64 64)"/><circle cx="16" cy="64" r="5.375" fill="#9EFF32" transform="rotate(315 64 64)"/><animateTransform attributeName="transform" type="rotate" values="0 64 64;315 64 64;270 64 64;225 64 64;180 64 64;135 64 64;90 64 64;45 64 64" calcMode="discrete" dur="880ms" repeatCount="indefinite"></animateTransform></g></svg>  <svg width="30" height="21" fill="currentColor"></svg>
          </div>
        </div>
            <div v-if="cartProductsLoadFailed">
              Произошла ошибка при загрузке товаров...
            </div>
            <CartItemComp
              v-for="item in products"
              :key="item.productId"
              :item="item"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters, mapState } from 'vuex';
import CartItemComp from '@/components/CartItemComp.vue';

export default {
  filters: {
    numberFormat,
  },
  components: { CartItemComp },
  computed: {
    ...mapGetters({
      products: 'cartDetailsProducts',
      totalPrice: 'cartTotalPrice',
    }),
    ...mapState({ cartProductsLoad: 'cartProductsLoad', cartProductsLoadFailed: 'cartProductsLoadFailed' }),
  },
  methods: {
    correctNameProdLabel() {
      if (this.$store.state.cartProducts.length === 1) {
        return 'товар';
        // eslint-disable-next-line max-len
      }
      if (
        this.$store.state.cartProducts.length > 1
        && this.$store.state.cartProducts.length < 5
      ) {
        return 'товара';
      }
      return 'товаров';
    },
  },
};
</script>

<style scoped>
.spiner {
  text-align: center;
}
</style>
